.container15 {
  display: grid !important;
  grid-template-columns: repeat(3, 10em);

  grid-template-rows: repeat(5, 5em);
  grid-auto-rows: 100px !important;
 
  /* justify-content: space-evenly;
  align-content: space-evenly; */
  text-align: center !important;

}

.box1 {
  grid-column-start: 1;
  grid-column-end: 2;
  grid-row-start: 1;
  grid-row-end: 2;
  width: 100%;
}
