}

.crossSign {
  top:0vw !important ;

  left: 12vw;
  position: absolute !important;

  cursor: pointer;
  height: 100px;
  width: 100px;

  color: #767674;
  content: "";
  display: block;
  font-family: icons;
  font-size: 0.75rem;
  font-weight: 400;
  text-indent: 0;
  font-size: 25px;
}
.crossSign:hover {
  color: white;
}

